import React from 'react';
import { useNavigate, useOutletContext, Link } from 'react-router-dom';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

import Title from '../component/Title';

function Register(props) {

  const [api, token, setToken, iter] = useOutletContext();
  const navigate = useNavigate();
  const [email, setEmail] = React.useState('hayden@unsw.edu.au');
  const [password, setPassword] = React.useState('1234567899(a');
  const [nameFirst, setNameFirst] = React.useState('Hayden');
  const [nameLast, setNameLast] = React.useState('Smith');

  React.useEffect(() => {
    if (token) {
      navigate('/a/quizzes');
    }
  }, [token]);

  const register = async () => {
    api.post(`/v1/admin/auth/register`, {
      email,
      password,
      nameFirst,
      nameLast
    }).then(data => {
      localStorage.setItem('token', data.token);
      setToken(data.token);
      navigate('/a/quizzes');
    });
  }

  return (<>
    <Title>Register</Title>
    Already signed up? <Link to="/a/login">Login now</Link>.
    <br />
    <br />
    <br />
    <TextField
      label="Email"
      value={email} 
      onChange={e =>
        setEmail(e.target.value)
      }
      type="text"
      sx={{
        width: '300px',
      }}
    /><br /><br />
    <TextField
      label="Password"
      value={password} 
      onChange={e =>
        setPassword(e.target.value)
      }
      type="password"
      sx={{
        width: '300px',
      }}
    /><br /><br />
    <TextField
      label="First Name"
      value={nameFirst} 
      onChange={e =>
        setNameFirst(e.target.value)
      }
      type="text"
      sx={{
        width: '300px',
      }}
    /><br /><br />
    <TextField
      label="Last Name"
      value={nameLast} 
      onChange={e =>
        setNameLast(e.target.value)
      }
      type="text"
      sx={{
        width: '300px',
      }}
    /><br /><br />
    <Button
      variant="contained"
      onClick={register}
    >
      Register
    </Button>
  </>);

}

export default Register;